@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width:100%;
}
html{
  scroll-behavior: smooth;
}
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
a{
  text-decoration:none;
}

.App-header {
  width:100%;
  max-width:100%;
  background-color: #0D0D0D;
  font-size: calc(10px + 2vmin);
  color: #595959;
  height:15vh;
  position:fixed;
  top:0;
  left:0;
  z-index:100;
}
.App-main{
  margin:0px;
  padding:0px;
  background-color:#0D0D0D;
  position:absolute;
  top:15vh;
  left:0;
  min-height:85vh;
  max-width:100%;
  width:100%;
}
.menu-main{
    display:flex;
    flex-direction:row;
    justify-items: center;
    align-items:left;
    max-width:100%;
    width:100%;
    height:100%;
}
#menu-selected{
    border-bottom:0.7vh solid #6C7339 !important;
}
.menu-main-name{
    padding-left:2vw;
    align-self:center;
    text-align:left;
    line-height:match-parent;
    width:20%;
    font-size:calc(3vh + 10px);
    font-weight:400;
    color:#595959;
}
.menu-main-name span{
    font-weight:700;
}
#menu-tab-links{
    margin-left:auto;
    align-self:center;
    height:30%;
    overflow:clip;
    line-height:match-parent;
    padding-right:2vw;
    display:flex;
    flex-direction:row;
    align-items:center;
    color:#D9D9D9;
    -webkit-transition:all .5s ease-in;
    transition:all .5s ease-in;
}
#menu-tab-links p{
    margin:0px;
    padding:0px;
    font-weight:600;
    font-size:1.5vw;
}

.body-main{
    background-color:#0D0D0D;
    max-width:100%;
    margin:0px;
    padding:0px;
    display:flex;
    flex-direction:column;
}

.maintext-main{
    height:85vh;
    max-width:100%;
    text-align:center;
}
.maintext-heading{
    color:#D9D9D9;
    font-size:5vw;
    padding:0;
    margin:0px;
    padding-top:5vh;
}
#maintext-txt2{
    color:#A4BF7A;
}
.maintext-maintext{
    font-size:2vw;
    color:#595959;
    display:flex;
    flex-direction:row;
    justify-content:center;
}
#maintext-txt4{
    opacity:0;
    color: #A4BF7A;
    -webkit-transition: opacity .3s ease-in;
    transition: opacity .3s ease-in;
}
#maintext-txt5{
    color:#D9D9D9;
    font-weight:500;
    -webkit-transition: opacity .3s ease-in;
    transition: opacity .3s ease-in;
}
.maintext-footer{
    margin-top:20vh;
}
#maintext-link-to-next{
    opacity:0;
    color:#595959;
    font-size:1.5vw;
    -webkit-transition: opacity 5s ease-in;
    transition: opacity 5s ease-in;
    -webkit-animation: changeColors .6s ease-in-out 0s infinite;
            animation: changeColors .6s ease-in-out 0s infinite;
}
#maintext-link-to-next:hover{
    cursor:pointer;
    -webkit-animation:none;
            animation:none;
    color:#A4BF7A;
}
#maintext-heading-dot{
    border-left:1vh solid #D9D9D9;
    -webkit-animation: dotdot .5s ease-in-out 0s infinite;
            animation: dotdot .5s ease-in-out 0s infinite;
}
#maintext-small-dot{
    display:none;
    border-left:1vh solid #D9D9D9 !important;
    -webkit-animation: dotdot .5s ease-in-out 0s infinite;
            animation: dotdot .5s ease-in-out 0s infinite;
}

@-webkit-keyframes dotdot{
    0%{
        border-left:1vh solid #0D0D0D;
    }
    100%{
        border-left:1vh solid #D9D9D9;
    }
}

@keyframes dotdot{
    0%{
        border-left:1vh solid #0D0D0D;
    }
    100%{
        border-left:1vh solid #D9D9D9;
    }
}
@-webkit-keyframes changeColors{
    0%{
        color:#595959;
    }
    25%{
        color:#A4BF7A;
    }
    50%{
        color:#D9D9D9;
    }
    100%{
        color:#6C7339;
    }
}
@keyframes changeColors{
    0%{
        color:#595959;
    }
    25%{
        color:#A4BF7A;
    }
    50%{
        color:#D9D9D9;
    }
    100%{
        color:#6C7339;
    }
}

@media only screen and (max-width: 992px) {
    .maintext-heading{
        font-size:8.5vw;
    }
    .maintext-maintext{
        font-size:4vw;
    }
    #maintext-link-to-next{
        font-size:2.5vw;
    }
}

