}

.hexagon-base1{
    display:flex;
    content:"";
    height:9vw ;
    width:5.15vw;
    background-color:#6C7339;
    -webkit-transform:rotate(60deg);
            transform:rotate(60deg);
    position:absolute;
    top:1vw;
    left:1vw;
}

.hexagon-base2{
    display:flex;
    content:"";
    height:9vw;
    width:5.15vw;
    background-color:#6C7339;
    -webkit-transform:rotate(-60deg);
            transform:rotate(-60deg);
    position:absolute;
    top:1vw;
    left:1vw;
}

.hexagon-main img{
    height:5.15vw;
    width:5.15vw;
